@keyframes revealBackground {
  0% {
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
  }
  10% {
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
  }
  100% {
    clip-path: polygon(0 0, 92% 0, 99% 100%, 0 100%);
  }
}

[data-animate] {
  opacity: 0;
  transform: translateY(30px); /* para fade-in-up */
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
  margin-top: 50px;
}

/* Fade In Up */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in-up {
  opacity: 1;
  transform: translateY(0);
  margin-top: 0;
  animation: fadeInUp 0.8s ease-out forwards;
}

/* Fade In Left */
@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.fade-in-left {
  opacity: 1;
  transform: translateY(0);
  margin-top: 0;
  animation: fadeInLeft 0.8s ease-out forwards;
}

/* Fade In Right */
@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.fade-in-right {
  opacity: 1;
  transform: translateY(0);
  margin-top: 0;
  animation: fadeInRight 0.8s ease-out forwards;
}

:root {
  font-family: "Poppins", sans-serif ;
  line-height: 1.6;
  font-weight: 400;
  
  --color-violet: #460182;
  --color-violet-ligth: #662D91;
  --color-violet-gray: #313131;
  --color-violet-ligther: #7e36b4;
  --color-violet-dark: #1E0035;
  --color-violet-darker: #0f011b;
  --color-white-violet: #fbf7fe;

	--color-gray-ligther: #e3e3e3;
	--color-gray-blue-dark: #3c4d66;
	--color-gray-blue-ligther: #9ab1cc;

  --color-black: #171717;

  background-color: var(--color-black);
  color: var(--color-white-violet);
}

body {
  margin: 0;
  font-family:"Poppins", sans-serif;
  font-weight: 400;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

b{
  font-family: "Playfair Display", serif;
  font-weight: 800;
  font-style: italic;
}

code {
  font-family: "Poppins", sans-serif;
}

h1{
  font-size: 36px;
}

h2{
  font-size: 30px;
  font-weight: 300;
}

h3{
  font-weight: 200;
  font-size: 22px;
}

h4,h6{
  font-weight: 200;
  font-size: 14px;
}

h5,li{
  font-weight: 200;
  font-size: 12px;
}

p{
  font-size: 16px;
}

span{
